<template>
<div>
    <b-card no-body id="main_div">
        <!-- Header  &  -->
        <b-card-header class="border-0" id="Header">
            <!--  -->
            <span id="title">Projects</span>

            <!--  -->
            <b-dropdown right id="setting" class="mr-5">
                <template v-slot:button-content>
                    <i class="ni ni-settings-gear-65"></i>
                </template>
                <b-dropdown-item id="show-modal" v-on:click="showModal = true">ADD</b-dropdown-item>
                <b-dropdown-item href="#">View</b-dropdown-item>
                <b-dropdown-item href="#">Schedules</b-dropdown-item>
            </b-dropdown>
        </b-card-header>

        
        <!-- Content   &  -->
        <el-table class="table-responsive table mt-5"
                  header-row-class-name="thead-light"
                  v-bind:data="projects">

            <!--   (Check) -->
            <el-table-column label="Check"
                             min-width="110px"
                             prop="check">
                <!--  -->
                <div class="d-flex align-items-center">
                    <b-form-checkbox v-model="checked" v-on:click="checked"></b-form-checkbox>
                </div>
                <div class="mt-3">
                    Checked: <strong>{{ checked }}</strong><br>
                </div>
            </el-table-column>

            <!--   (Project Title) -->
            <el-table-column label="Project Title"
                             prop="project_title"
                             min-width="310px"
                             v-slot="{row}">
                             <span class="title">{{ row.project_title }}</span>
            </el-table-column>


            <!--   (Status) -->
            <el-table-column label="Status"
                             min-width="150px"
                             prop="project_status"
                             v-slot="{row}">
                             <badge class="badge-dot mr-4" type="">
                                <i :class="`bg-${row.project_statusType}`"></i>
                                <span class="status" :class="`text-${row.project_statusType}`">{{ row.project_status }}</span>
                             </badge>
            </el-table-column>


            <!--   (Users) -->
            <el-table-column label="Users"
                             min-width="120px"
                             prop="project_users"
                             v-slot="{row}">
                             {{ row.project_users }}
            </el-table-column>


            <!--   (Completion) -->
            <el-table-column label="Completion"
                             min-width="240px"
                             prop="project_completion"
                             v-slot="{row}">
                             <div class="d-flex align-items-center">
                                <div>
                                    <base-progress :type="row.project_statusType" :value="row.project_completion"/>
                                </div>
                                <span class="completion ml-2">{{ row.project_completion }}%</span>
                             </div>
            </el-table-column>


            <!--    (Delete) -->
            <el-table-column label="Delete"
                             min-width="120px"
                             prop="delete" >
                <!--  -->
                <template slot-scope="test">
                    <base-button native-type="submit" size="sm" outline type="default" id="remove_Btn" v-on:click.native.prevent="deleteRow(test.$index, projects)">
                        <i class="ni ni-fat-remove" id="remove"></i>
                    </base-button>
                </template>
            </el-table-column>
        </el-table>

    </b-card>
    <!-- Add Modal -->
    <AddPage v-if="showModal" @close="showModal = false">
    </AddPage>
</div>
</template>


<script>
/* eslint-disable */
//import projects from './../projects'
import { Table, TableColumn } from 'element-ui';
import BaseCheckbox from '../../../components/Inputs/BaseCheckbox.vue';
import BaseButton from '../../../components/BaseButton.vue';
import AddPage from "../Modal/AddPage.vue";
import axios from 'axios'
import Swal from 'sweetalert2'
import Vue from 'vue';
import Vuex from 'vuex';
import createPersistedState from "vuex-persistedstate";
Vue.use(Vuex); 

// Vuex  - Store: (state[ Data], mutations[-], actions[-], getter[])
const store = new Vuex.Store({
    //     , Vuex     
    plugins: [
      createPersistedState()
    ],
    //      (commit)
    mutations: {
      setProjects(state, payload) {
        console.log("Set Projects Now... (Title) ");
        state.projects = payload.projects;
      }
    }
});

export default {
    //   : <light-table />
    name: 'light-table',
    components: {
        BaseCheckbox,
        BaseButton,
        AddPage,
        [Table.name]:Table,
        [TableColumn.name]: TableColumn
    },
    data() {
        console.log("data");
        return {
            currentPage: 1,
            showModal: false,
            projects: this.$store.state.projects,
            checked: false
        }
    },
    methods: {
        checked() {
            console.log("checked");
        },
        deleteRow(index, rows) {

            //   
            axios({
                method: "DELETE",
                url: '/deleteProject',
                data: {
                    "index": this.$store.state.projects[index].project_index
                } 
            })
            .then((response) => {
                console.log(": " + response.data);
                Swal.fire({
                    title: 'success!',
                    text: '.',
                    icon: 'success',
                    confirmButtonText: ''
                });
            })
            .catch((error) => {
                console.log("Error: " + error);
            });

            //   
            rows.splice(index, 1);
            var temp = this.$store.state.projects
            
            // Vuex  
            console.log("before: " + temp[index]);
            temp.splice(index, 0);

            console.log("after: " + temp[index]);
            store.commit('setProjects', {
                projects: temp
            });
        }
    }
}
</script>


<style scoped>
#main_div {
    padding-top: 0.5rem;
}
#Header {
    display: inline;
    block-size: 0%;
}

#title {
    font-size: 1.2rem;
    font-weight: bold;
}

#setting {
    float: right;
    margin: 0px;
    padding: 0px;
    block-size: 0px;
    inline-size: 0px;
    width: 0px;
    height: 0px;
    border: 0px;
    box-shadow: 0px;
    background-color: rgb(165, 67, 67);
    border: 0px solid white;
    box-shadow: none;
}

#setting-button {
    margin: 0px;
    padding: 0px;
    block-size: 0px;
    inline-size: 0px;
    width: 0px;
    height: 0px;
    border: 0px;
    box-shadow: 0px;
}

#remove_Btn {
    border-style: none;
    margin-left: 0.8rem;
}

#remove {
    font-size: 1rem;
}

.el-table .warning-row {
    background: oldlace;
}

.el-table .success-row {
    background: #f0f9eb;
}

.title {
    text-decoration: line-through;
}
</style>